.input {
	display: block;
	width: 400px;
	padding: 0 1rem;
	border-radius: 0.25rem;
	border: 1px solid;
	border-color: inherit;
	background-color: #fff;
}
.sm {
	height: 2rem;
	font-size: 0.875rem;
}
.md {
	height: 2.5rem;
	font-size: 1rem;
}
.lg {
	height: 3rem;
	font-size: 1.25rem;
}
